@import '../variables.scss';

.Container {
    padding: 0 40px;
    @media (max-width: 575.98px) {
        padding: 0;
    }
}
.Section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 50px 20px 50px 20px;
    @media (max-width: 575.98px) {
        margin: 0;
    }
}
.Developer {
    align-items: flex-end;
    background-color: $third-color;
    display: flex;
    height: 594px;
    justify-content: center;
    margin-right: 120px;
    @media (max-width: 575.98px) {
        margin: 0 0 30px 0;
    }
    @media (max-width: 380px) {
        min-width: 300px;
    }
    padding-bottom: 23px;
    position: relative;
    min-width: 366px;
}
.Text {
    @include eng-title;
    left: 202px;
    position: absolute;
    top: 67px;
    @media (max-width: 575.98px) {
        left: 10px;
    }
}
.Skills {
    align-items: center;
    background-color: $primary-transparent-color;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    min-height: 564px;
    padding: 30px;
    max-width: 786px;
    @media (max-width: 575.98px) {
        margin-bottom: 30px;
        min-width: 300px;
    }
}
.Education {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
