@import '../../variables.scss';

.List {
    @include rus-text;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.Title {
    @include rus-title;
    margin: 50px 0;
    text-align: center;
}
.Item {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: 20px;
    width: 130px;
}
.Logo {
    height: 73px;
    width: 73px;
    margin-bottom: 10px;
}
